// getCroppedImg.js

import RGBQuant from 'rgbquant';
import UPNG from 'upng-js';

export default function getCroppedImg(imageSrc, pixelCrop, outputSize) {
  return new Promise(async (resolve, reject) => {
    const image = new Image();
    image.crossOrigin = 'Anonymous'; // CORS  
    image.src = imageSrc;

    image.onload = () => {
      const canvas = document.createElement('canvas');
      canvas.width = outputSize.width;
      canvas.height = outputSize.height;
      const ctx = canvas.getContext('2d');

      //   
      ctx.imageSmoothingEnabled = true;
      ctx.imageSmoothingQuality = 'high';

      //   
      ctx.fillStyle = '#fff';
      ctx.fillRect(0, 0, canvas.width, canvas.height);

      //  
      ctx.drawImage(
        image,
        pixelCrop.x,
        pixelCrop.y,
        pixelCrop.width,
        pixelCrop.height,
        0,
        0,
        canvas.width,
        canvas.height
      );

      //   
      const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);

      // RGBQuant 
      const quantizer = new RGBQuant({
        colors: 6, //   
        method: 2, //   
        initColors: 6,
        minHueCols: 0,
        dithKern: 'Atkinson', // Atkinson  
        // dithDelta: 0.1,
        dithSerp: false, //   
        palette: [
          [0, 0, 0],         // Black
          [255, 255, 255],   // White
          [0, 255, 0],       // Green
          [0, 0, 255],       // Blue
          [255, 0, 0],       // Red
          [255, 255, 0],     // Yellow
        ],
        colorDist: "manhattan",
      });

      //   
      quantizer.sample(imageData);

      //  
      const quantizedData = quantizer.reduce(imageData);

      //    
      const newImageData = new ImageData(
        new Uint8ClampedArray(quantizedData),
        canvas.width,
        canvas.height
      );

      // //    
      // ctx.putImageData(newImageData, 0, 0);

      // // Blob  
      // canvas.toBlob(
      //   (blob) => {
      //     if (!blob) {
      //       reject(new Error('Canvas is empty'));
      //       return;
      //     }
      //     blob.name = 'croppedImage.png';
      //     const croppedImageUrl = window.URL.createObjectURL(blob);
      //     resolve(croppedImageUrl);
      //   },
      //   'image/png',
      //   1
      // );

      const rgba = newImageData.data.buffer;
      const width = newImageData.width;
      const height = newImageData.height;
      //    
      const palette = quantizer.palette();
      // flat()  [].concat(...palette) 
      const flatPalette = [].concat(...palette);

      const pngData = UPNG.encode([rgba], width, height, 16, [], flatPalette);

      // Blob 
      const blob = new Blob([pngData], { type: 'image/png' });

      // Blob URL   
      const croppedImageUrl = window.URL.createObjectURL(blob);
      // console.log(blob);
      resolve(croppedImageUrl);
    };

    image.onerror = (error) => {
      reject(error);
    };
  });
}
